import styled from 'styled-components';

export const WrapperForm= styled.div`
    .buttom-send {
        display: flex;
        justify-content: flex-end;
        margin-bottom: 28px;
    }
    & .section-container-footer {
        width: 100%!important;
    }
    & .ant-btn.my-buttom.ant-btn-primary {
        height: 29px !important;
        margin-bottom: 0px;
    }
    & .ant-row-flex-middle {
        
        margin-top: -5px!important;
    }
    & .ant-row form-fields{
        margin-bottom: -20px!important;
    }
`;
export const WraperReserveSearch= styled.div`
    & .section-container-search{
        padding-top:10px;
    }
`;
export const Wrapper = styled.div`
    & .link{
        display: flex;
        font-family: 'Jost';
        font-size: 17px;
        font-weight: 500;
        letter-spacing: 0px;
        opacity: 1;
        padding-top: 10px;
        margin-bottom: 30px;
    }
    & .anticon-arrow-left {
        margin-right: 26px;
        padding-top: 8px;
        align-items: center;
        display: contents;
    }
    & .title {
        font-size: 17px;
        font-family: 'Jost';
        font-weight: 600;
        letter-spacing: 0px;
        color: #A734FF;
        opacity: 1;
    }
    & .icon svg {
        margin-right: 8px;
    }
    .name h2{
        font-size: 28px;
        font-family: 'Jost';
        font-weight: 600;
        letter-spacing: 8.4px;
        color: #000000;
        text-transform: uppercase;
        opacity: 1;
        text-align: left;
        margin-bottom: -7px;
    }
   

`;