import React, { useState, useEffect } from "react";
import{WraperReserveSearch}from "./Styles";
import { Col, Layout, Row, Divider, Avatar, Button, Card, Input } from "antd";
import Banner from "../../components/banner/Banner";
import MyListReservations from "../../components/my-list/MyListReservations";
import { translate } from "react-polyglot";
import Loader from "../../components/loader/";
import { useSelector, useDispatch } from "react-redux";
import SearchField from "../../components/search-field/SearchField";


const Reservations = ({ t, props }) => {
    const [loading, setLoading] = useState(true)
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    });
    const user = useSelector(({ auth }) => {
        return auth.user;
    });
    /* if (loading)return <Loader/> */
    // if (!user) return "Loading...";
    return (
        <WraperReserveSearch>
            <Banner dataSource={[{
                title: t("reservation.banner.title"),
                image_path: "/images/MisReservas.jpg",
                thubmnail: "/images/banner-min.png"
            }]} />
            <Row className="section section-search" type="flex" justify="center" align="center" gutter={10}>
                <Col className="section-container-search" xl={10} lg={16} md={16} xs={22} >
                    <SearchField
                        placeholder={t("search.Text.Input")}
                        reference="packs"
                        open={false}
                        source={`name_${locale}`}
                    />
                </Col>
            </Row>
            <Row style={{ margin: "40px 10px" }} type="flex" justify="center">
                <Col xl={16} lg={16} md={24} xs={24}>
                    <MyListReservations
                        reference="reservations"
                        filterDefaultValues={{
                            user_id: user.id,
                            $sort: {
                                createdAt: -1
                            }
                        }}
                    />
                </Col>
            </Row>
        </WraperReserveSearch>
    );
};
export default translate()(Reservations);