import React, { useState, useEffect } from "react";
import { WrapperReservationsList } from "./Styles"
import { Carousel, Card, Icon, Row, Col, Divider, Button, Avatar, Layout, List, Skeleton, Pagination, Progress, message } from "antd";
import { navigate } from "@reach/router"
import Grid from "antd/lib/card/Grid";
import { isMobile } from "react-device-detect";
import { translate } from "react-polyglot";
import { useSelector, useDispatch } from "react-redux";
import { getService } from "../../services/";
import { money, momentLocale } from "../../utils/";
import Loader from "../../components/Loader";
const format = 'MMMM Do YYYY, h:mm:ss A'
const status = [
    {
        id: "aprobada",
        name: "Aprobada",
    },
    {
        id: "cancelada",
        name: "Cancelada",
    },
    {
        id: "esperando_aprobacion",
        name: "Esperando Aprobación",
    },
];
const MyListReservations = ({ t, reference, filterDefaultValues = {}, ...props }) => {
    const [dataSource, setDataSource] = useState([]);
    const [loading, setLoading] = useState(true);
    const locale = useSelector(({ auth }) => {
        return auth.locale;
    });
    const entity = useSelector(({ auth }) => {
        return auth.entity;
    });
    const user = useSelector(({ auth }) => {
        return auth.user;
    });
    let moment = momentLocale(locale);
    const handleClick = (id) => {
        navigate(`/reservations/${id}`);
    }
    const getData = () => {
        if (reference) {
            const service = getService(reference);
            setLoading(true);
            service.find({
                query: {
                    ...filterDefaultValues
                }
            })
                .then(({ data }) => {
                    setDataSource(data);
                    setLoading(false);
                })
                .catch(err => {
                    setLoading(false);
                    message.error(err.message)
                });
        }
    }
    /* const getEntity = (id) => {
        if (reference) {
            const service = getService("entities");
            service.get(id)
                .then(entity => {
                    
                })
                .catch(err => {
                    message.error(err.message)
                });
        }
    } */
    useEffect(() => {
        if (props.dataSource)
            setDataSource(props.dataSource);
    }, [props.dataSource]);

    useEffect(() => {
        if (reference)
            getData();
    }, []);
    /* if (loading) return <Loader/> */
    if (!user) return null;
    return (
        <WrapperReservationsList>
            <List
                className="demo-loadmore-list"
                loading={loading}
                itemLayout={isMobile ? "vertical" : "horizontal"}
                //loadMore={loadMore}
                dataSource={dataSource}
                renderItem={item => {
                    let current_status = status.find((it) => (it.id == item.status))
                    return (
                        <List.Item
                            Layout="vertical"
                            actions={[<a key="list-loadmore-more">
                                <Button
                                   disabled={item.reservation_status_id != 1} 
                                    onClick={() => handleClick(item.id)}
                                    type="ghost" key="reservations-detail">{t("showbuttonText")}</Button>
                            </a>]}
                        >
                            <Skeleton avatar title={false} loading={item.loading} active>
                                <Row className="skeleton" gutter={16} justify="space-between" align="middle" type="flex">
                                    {/*  <Col xl={6} lg={8} md={8} sm={8} xs={24}>
                                        <div className="title"><a>{item.title}</a></div>
                                    </Col> */}
                                    <Col xl={10} lg={10} md={10} sm={8} xs={24}>
                                        <div className="date-container">
                                            <img src={"/images/Icon feather-calendar.png"} />
                                            <div className="date">
                                                <span>{
                                                    item.date ? moment(item.date).format(format) :
                                                    moment(item.createdAt).format(format)
                                                }</span>
                                            </div>
                                        </div>
                                    </Col>
                                    <Col xl={6} lg={6} md={6} sm={8} xs={24}>
                                        <div className="prices"><span>{money(item.total_price_cop)}</span></div>
                                    </Col>
                                    <Col xl={8} lg={8} md={6} sm={8} xs={24}>
                                        {<div className={`status ${item.reservations_status && item.reservations_status.name.toLowerCase()}`}>
                                            <div className="satus-indicator" />
                                            {item.reservations_status &&
                                                <div className="status-name">{item.reservations_status[`title_${locale}`]}
                                                </div>}
                                        </div>}
                                    </Col>
                                </Row>
                            </Skeleton>
                        </List.Item >

                    )
                }
                }
            />
            {/* <Pagination defaultCurrent={1} total={50} /> */}
        </WrapperReservationsList >
    );
};
export default translate()(MyListReservations);